html, body, .root {
  height: 100%;
}
body {
  line-height: 1.5;
}
body, .app {
  background: #A9BCD0;
  position: relative;
  min-height: 100vh;
}

/* main page */
.list-books {
  padding-bottom: 200px;
}
.list-books-title {
  padding: 15px 0;
  margin-bottom: 30px;
  background: #1b1b1e;
  text-align: center;
  text-transform: uppercase;
}
.list-books-title h1 {
  font-weight: 400;
  margin: 0;
  color: #58A4B0;
}

.list-books-content {
  padding: 0 0 80px;
  -ms-flex: 1 1;
      flex: 1 1;
}

.bookshelf {
  margin: 0 5px;
  padding-bottom: 25px;
  background: -webkit-gradient(linear, left top, left bottom, from(#373F5132), to(#00000000));
  background: -webkit-linear-gradient(top, #373F5132, #00000000);
  background: -o-linear-gradient(top, #373F5132, #00000000);
  background: linear-gradient(to bottom, #373F5132, #00000000);
  border-radius: 20px;
}

@media (min-width: 600px) {
  .bookshelf {
    margin: 0 20px;
    padding-bottom: 60px;
  }
}

.bookshelf-title {
  padding: 10px 0 0 20px;
  margin-bottom: 0;
}
.bookshelf-books {
  text-align: center;
  padding-top: 15px;
}

.open-search {
  position: fixed;
  right: 25px;
  bottom: 25px;
}
.open-search a {
  display: block;
  width: 50px;
  height: 50px;
  border-radius: 50%;
  background: #58A4B0;
  background-image: url('./icons/add.svg');
  background-repeat: no-repeat;
  background-position: center;
  background-size: 28px;
  -webkit-box-shadow: 0 5px 18px -7px #1b1b1e;
          box-shadow: 0 5px 18px -7px #1b1b1e; 
  font-size: 0;
}
.open-search:hover a{
  -webkit-transform: scale(1.1);
      -ms-transform: scale(1.1);
          transform: scale(1.1);
  -webkit-box-shadow: 0 4px 18px -8px #1b1b1e;
          box-shadow: 0 4px 18px -8px #1b1b1e;
}
/* search page */

.search-books-bar {
  position: fixed;
  width: 100%;
  top: 0;
  left: 0;
  z-index: 5;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-shadow: 0 10px 20px rgba(0,0,0,0.19), 0 0 6px rgba(0,0,0,0.23);
          box-shadow: 0 10px 20px rgba(0,0,0,0.19), 0 0 6px rgba(0,0,0,0.23);
}
.search-books-input-wrapper {
  -ms-flex: 1 1;
      flex: 1 1;
  background: #e9e;
}
.search-books-bar input {
  width: 100%;
  padding: 15px 10px;
  font-size: 1.25em;
  border: none;
  outline: none;
}

.close-search {
  display: block;
  top: 20px;
  left: 15px;
  width: 50px;
  background: white;
  background-image: url('./icons/arrow-back.svg');
  background-position: center;
  background-repeat: no-repeat;
  background-size: 28px;
  font-size: 0;
}

.search-books-results {
  padding: 80px 10px 240px;
}

/* books grid */

.books-grid {
  list-style-type: none;
  padding: 0;
  margin: 0;

  display: -ms-flexbox;

  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
}
.books-grid li {
  margin: 10px 10px;
  text-align: left;
  background-color: #eee;
  border-radius: 7px;
}

.book {
  display: grid;
  width: 150px;
  height: 100%;
  padding: 10px 10px 5px 10px;
  -webkit-box-shadow: 0 5px 20px rgba(0, 0, 0, 0.2);
          box-shadow: 0 5px 20px rgba(0, 0, 0, 0.2);
  border-radius: 7px;
}
.book-title,
.book-authors {
  font-size: 0.8em;
}
.book-title {
  margin-top: 13px;
  -ms-flex-item-align: center;
      align-self: center;
}
.book-details {
  -ms-flex-item-align: end;
      align-self: end;
}
.book-shelf {
  margin: 10px 0 3px 0;
  text-align: right;
  font-size: 0.55em;
  font-weight: 700;
  color: #58A4B0;
}
.book-authors {
  font-size: 0.65em;
  border-top: 1px solid #58A4B0;
  color: #999;
}
.book-authors span {
  font-weight: 700;
}

.book-top {
  position: relative;
  height: 200px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
}

.book-shelf-changer {
  position: absolute;
  right: -5px;
  bottom: -17px;
  width: 40px;
  height: 40px;
  -webkit-box-sizing: content-box;
          box-sizing: content-box;
  border: 7px solid #eee;
  border-radius: 50%;
  background: #58A4B0;
  background-image: url('./icons/sort.svg');
  background-repeat: no-repeat;
  background-position: center;
  background-size: 25px;
}
.book-shelf-changer:hover {
  -webkit-transform: scale(1.05);
      -ms-transform: scale(1.05);
          transform: scale(1.05);
}
.book-shelf-changer select {
  width: 100%;
  height: 100%;
  opacity: 0;
  cursor: pointer;
}

/* book cover */

.book-cover {
  -webkit-box-shadow: 0 0 15px -5px #373F51;
          box-shadow: 0 0 15px -5px #373F51; 
  background: #eee;
  border-radius: 7px 7px 0 0;
}
.book-cover-title {
  padding: 20px 10px 0;
  text-align: center;
  font-size: 0.8em;
}

/* footer */
footer {
  position: absolute;
  bottom: 0;
  width: 100%;
  padding: 55px;
  text-align: center;
  color: #999;
  background-color: #1b1b1e;
}
footer p {
  margin: 0 0 10px;
}
@media only screen and (min-width: 600px) {
  footer {
    font-size: 1.1em;
  }
  footer p {
    margin: 0 0 5px;
  }
}


